interface User {
    imie:string;
    nazwisko:string;
    wiek:number;
}

function addUser(imie: string, nazwisko: string, wiek: number): User{
    return{
        imie: imie,
        nazwisko: nazwisko,
        wiek: wiek
    }
}

let user: User;

let form = document.getElementById('formularz');
form.addEventListener('submit',(ev)=>{
    let inputElements = document.querySelectorAll('#formularz input');
    let p = new Promise(function (resolve, reject){
        user = addUser((<HTMLInputElement>inputElements[0]).value,
            (<HTMLInputElement>inputElements[1]).value,
            parseInt((<HTMLInputElement>inputElements[2]).value)
        );
        resolve(user);
    }).then((data)=>{
        fetch("/toServer", {
            method: "post",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        })
            .then( (response) => {
                console.log('wyslano! odpowiedz serwera: ->', response);
            });
    })
    event.preventDefault();
})











